<script>
import LayoutWebMobile from '@layouts/LayoutWebMobile'
import GxWizard from '@components/_CoreTaxes/Wizard/GxWizard'
import { GET_ACCOUNTING_FIRMS } from '@src/apollo/queries'
import { CONFIG_END_CONFIG } from '@src/apollo/mutations'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'

export default {
  name: 'FinishConfiguration',
  components: {
    LayoutWebMobile,
    GxWizard,
    // GxDropdown,
    GxCardModal,
  },
  data() {
    return {
      accountingFirm: null,
      accountingFirmNumber: null,
      clientNumber: null,
      loading: false,
      loadingBack: false,
      loadingNext: false,
      errorDescription: null,
      steps: [
        'Elige tu catálogo',
        'Personaliza tu catálogo',
        'Periodo inicial',
        'Saldos iniciales',
        '¡Listo!',
      ],
    }
  },
  created() {
    this.clientNumber = localStorage.getItem('companyNumber')
  },
  computed: {},
  apollo: {
    myAccountingFirms: {
      query: GET_ACCOUNTING_FIRMS,
    },
  },
  watch: {
    myAccountingFirms: {
      handler(newVal) {
        if (newVal) {
          this.accountingFirm = newVal[0]
          this.accountingFirmNumber = this.accountingFirm.accountingFirmNumber
          this.skipLedgerAccounts = false
        }
      },
    },
  },
  methods: {
    closeModel() {
      this.errorDescription = ''
    },
    async finishConfiguration() {
      //console.log('error')
      this.loadingNext = true

      try {
        const response = await this.$apollo.mutate({
          mutation: CONFIG_END_CONFIG,
          variables: {
            accountingFirmNumber: this.accountingFirmNumber,
            clientNumber: this.clientNumber,
          },
        })
        this.loadingNext = false
        //console.log(response)

        if (
          response.data.configEndConfig.successful &&
          response.data.configEndConfig.error === null
        ) {
          localStorage.removeItem('companyNumber')
          localStorage.removeItem('legalName')
          localStorage.removeItem('taxId')
          localStorage.removeItem('migration_tax_id')
          localStorage.removeItem('configurationType')

          //console.log('test')
          this.$router.push({
            name: 'dashboard',
          })
        } else {
          this.errorDescription =
            response.data.configEndConfig.error.message +
            ' - ' +
            response.data.configEndConfig.error.details.join('')
        }
        this.loading = false

        //console.log(response)
      } catch (error) {
        console.log(error)
        return null
      }
    },
    goBack() {
      this.$router.push({
        name: 'previewTemplate',
      })
    },
    goNext() {
      this.$router.push({
        name: 'beginningBalance',
      })
    },
  },
}
</script>
<template>
  <LayoutWebMobile icon="keyboard_backspace" :icon-click-handler="goBack">
    <div class="flex flex-col w-full items-center">
      <div class="flex flex-col text-xl mt-3"
        ><span class="font-black">Configuración Inicial</span></div
      >
      <div class="flex flex-col mt-10">
        <GxWizard :stepsProp="steps" :stepActiveProp="6" />
      </div>

      <div class="flex flex-col mt-10 justify-between w-100 items-center ">
        <figure>
          <img
            class="w-44 h-44"
            src="@assets/images/DE/Login/de_002_bienvenido_temporal.svg"
            alt="CASHME"
        /></figure>
        <h1 class="mb-3 mt-4 font-bold text-center de:gx-h4"
          >En sus marcas,listos, ¡fuera!</h1
        >
        <p class="mb-4 text-center text-base"
          >¡Es momento de llevar la contabilidad a la velocidad de la luz!</p
        >
        <div class="flex flex-col ml-9">
          <GxButton
            class="mr-10 mt-4 gx-button"
            :class="{ ' pl-6 pr-6': !this.loadingNext }"
            @click="this.finishConfiguration"
            :loading="this.loadingNext"
          >
            IR A LA CONTABILIDAD
          </GxButton>
        </div>
      </div>
    </div>

    <GxCardModal
      v-if="errorDescription"
      :headline="'Se presentaron algunos errores al procesar la solicitud'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="closeModel">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </LayoutWebMobile>
</template>
